.skills{
  padding-top: 4rem;
}
.mid {
    text-align: center;
    padding: 20px;
    margin-top: -5px;
  
  }
  .color {
    color: #d1b561;
    /* color: #ffffff; */
    width: 100px;
    height: 70px;
    margin-left: -3px;
  }
  .flex {
    display: flex;
    margin-left: 20%;
    padding-bottom: 20px;
    text-align: center;
  }
  .size
  {
    width: 150px;
    height: 120px;
    text-align:center;
    padding-top: 20px;
    border-radius: 10px;
    border: 1px solid #d1b561;
    /* border: 1px solid #ffffff; */
    transition: transform .2s;
    margin:30px
  }
  .size:hover
  {
    transform: scale(1.1);
  }
  @media (max-width: 900px) {
    .flex {
      display:unset;
    }
    .mid {
      text-align:center;
      padding: 20px;
      margin-top: -10px;
    }
    .size{
      margin-left: 35%;
    }
  
  } 