.experience{
    padding-top: 4rem;
}
.mid {
    text-align: center;
    padding: 20px;
    margin-top: -5px;
}

.experience_wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 5vw;

}

.experience_container{
    margin: 20px auto;
    border: 1px solid grey;
    border-radius: 10px;
    transition: transform .2s;
    padding: 0 5vw;
    width: 80%;
}

.experience_container:hover{
    transform: scale(1.04)
}

/* .experience_container > div{
    padding: 10px 0;
} */

.company_container{
    display: flex;
    padding: 10px 0 3px 0; 
    flex-wrap: wrap;
}

.company_container > div > span:first-child, .company_container > div > span:nth-child(1){
    font-size: 24px;
    font-weight: bold;
}

.company_container > div:first-child >  span:first-child{
    color: #d1b561;
}

.company_container > div:nth-child(2) > span:nth-child(2){
    color: #fff;
}

.company_container > div:nth-child(2) > span:nth-child(2){
    color: #fff;
    font-weight: bold;
    font-size: 18px;
}

.company_container > div:nth-child(2) > p{
    padding: 5px 0 0 0;
    margin: 0;
    box-sizing: border-box;
    font-size: 14px;
}

.responsibilities_container{
    /* display: flex; */
    /* align-items: center;
    flex-wrap: wrap; */
}

.responsibilities_container > div > p{
    color: #d1b561;
    font-size: 24px;
    font-weight: bold;
}

.responsibilities_container > ul > li{
    font-size: 18px;
    font-weight: 500;
    padding: 5px 0;
}


